:root {
  --animation-duration: 0.1s;
  --sidebar-background-color: #f5f5f5;
  --sidebar-inactive-color: #272727;

  --white: #fff;
  --light-background-color: #fafafa;
  --grey-outline: #bdbdbd;

  /* Bootstrap 5 screen widths */
  --screen-width-sm: 576px;
  --screen-width-md: 768px;
  --screen-width-lg: 992px;
  --screen-width-xl: 1200px;
  --screen-width-xxl: 1400px;

  --font-sans: "Nunito Sans", sans-serif;
  --font-serif: "Lora", serif;
}

body {
  font-family: var(--font-sans);
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.sidebar-target {
  color: transparent;
  text-shadow: 0 0 0 var(--sidebar-inactive-color);
  transition: color var(--animation-duration) linear;
}

.sidebar-target-active,
.sidebar-target:hover {
  color: inherit;
}

.page-section {
  display: flow-root;
}

.page-section:nth-child(odd) {
  background-color: var(--white);
}

.page-section:nth-child(even) {
  background-color: var(--light-background-color);
}

.page-section > p {
  font-size: 1.1rem;
  font-family: var(--font-serif);
  font-style: italic;
  max-width: var(--screen-width-md);
  margin: 2em auto;
  line-height: 1.7;
  text-align: center;
}

.page-section > h1 {
  margin: 0.8em auto 1em;
  width: var(--screen-width-xl);
}

.profile-section {
  display: flex;
  margin: auto;
  max-width: var(--screen-width-xl);
  height: 100vh;
  /* maybe set height to max(100vh, some limit) instead?
     for when client zooms out to like 25% or sth */
}

@media screen and (max-width: 992px) {
  .profile-section {
    flex-direction: column;
  }
}

.profile-info,
.profile-avatar {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin: 2em auto;
  padding: 0 2em;
  max-width: var(--screen-width-sm);
  justify-content: center;
  gap: 1em;
}

.profile-info {
  align-items: start;
}

.profile-avatar {
  align-items: center;
}

.profile-avatar > img {
  /* align-self: center; */
  max-width: 80%;
}

.showcase-container {
  display: grid;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  grid-gap: 1rem;
  /* display: flex; */
  /* overflow-x: auto; */
  /* display: grid;
  grid-auto-flow: column;
  grid-auto-columns: minmax(0, 1fr); */

  /* display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr; */

  width: fit-content;
  max-width: var(--screen-width-xl);
  margin: auto;

  /* align-items: center; */
  /* justify-content: space-between; */

  /* max-height: var(--screen-width-xl); */
  /* will become width once rotated */
  /* width: 200px;
  transform: rotate(-90deg) translateY(calc(0px - 200px));
  transform-origin: right top;
  overflow-y: auto;
  overflow-x: hidden; */
}

.showcase-element,
.education-element,
.contact-element {
  border: 2px solid var(--grey-outline);
  border-radius: 8px;
  padding: 1em 1.1em;

  /* width: 100px;
  transform: rotate(90deg);
  transform-origin: right top; */
}

.showcase-element h2,
.education-element h2,
.contact-element h2 {
  margin: 0 0 0.5em;
  font-size: 1.3em;
}

.contact-element h2 {
  text-align: center;
}

.education-container,
.contact-container {
  display: grid;
  grid-gap: 1rem;
  margin: auto;
}

.education-container {
  grid-template-columns: repeat(2, minmax(0, 1fr));

  width: fit-content;
  max-width: var(--screen-width-xl);
}

.contact-container {
  grid-template-columns: minmax(0, 4fr) minmax(0, 7fr);

  width: var(--screen-width-lg);
  margin: auto;
}